.charts-cotainer{
    width:94%;
    margin:auto;
}
.chart-header{
    display: flex;
    justify-content: center;
    margin:1rem auto;
}
.chart-header h2{
    font-size: 1.1rem;
}
.selected-country{
    width:100%;
    margin:1rem auto;
    padding:1rem;
    box-shadow: 0 0 20px 1px rgba(0,0,0,0.09);
}
.selected-country h3{
    margin-bottom:1rem;
    color:var(--theme-color);
    text-transform: uppercase;
}
.selected-country .data{
    display: flex;
    justify-content: space-around;
}
.selected-country .data div p:nth-child(1){
    font-size: .9rem;
}
.selected-country .data div p:nth-child(2){
    margin-top:.5rem;
    font-weight: bold;
    font-size: 1.5rem;
}
.selected-country .cases{
    color:var(--info);
}
.selected-country .deaths{
    color:var(--danger);
}
.selected-country .recovered{
    color:var(--success);
}
.selected-country .active{
    color:var(--warning);
}