nav{
    display: flex;
    background:rgba(238, 238, 238,0.5);
    height:60px;
    padding:1rem;
    align-items: center;
    font-family: 'Open Sans', sans-serif;
}
nav .logo{
    font-size:2rem;
    font-weight: bold;
    color:var(--theme-color);
}
nav ul{
    list-style: none;
    display: flex;
    margin-left:2rem;
}
nav a{
    display: block;
    text-decoration: none;
    padding:8px;
    text-transform: uppercase;
    font-weight: bold;
    color:#ccc;
}